*,
*:after,
*:before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}
body{
  margin: 30px;
}
html{
  font-size: 62.5%;
  box-sizing: border-box;
  margin: 0;
}
header{
  position: relative;
  background-color: #100e17;
  height: 12rem;
  color: rgb(255, 255, 255);
}
.navBar{
  display:flex;
  width: 100%;
}
.flexItem{
  border-left: 1px solid white;
  height: 12rem;
}
.flexItem span{
  display: block;
}
.logo{
  order: 1;
  flex-grow: 2;
}
.logo img{
  width: 20px;
}
.aboutLink{
  order: 2;
  flex-grow: 1;
}
.portfolioLink{
  order: 3;
  flex-grow: 1;
}
.resumeLink{
  order: 4;
  flex-grow: 1;
}
.emailLink{
  order: 5;
  flex-grow: 1;
}
.gitLink{
  order: 6;
  flex-grow: 1;
}
